import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';

// Configuracin para integracin con Softr
const SoftrIntegration = () => {
  // Estado para almacenar datos de Softr
  const [softrData, setSoftrData] = React.useState(null);
  
  React.useEffect(() => {
    // Notificar a Softr que la aplicacin est lista
    if (window.parent !== window) {
      window.parent.postMessage({ type: 'reactAppReady' }, '*');
    }
    
    // Escuchar mensajes de Softr
    const handleMessage = (event) => {
      // En produccin, verifica el origen
      // if (event.origin !== 'https://tusitiosoftr.com') return;
      
      const { type, data } = event.data || {};
      if (type === 'softrData') {
        setSoftrData(data);
      }
    };
    
    window.addEventListener('message', handleMessage);
    return () => window.removeEventListener('message', handleMessage);
  }, []);
  
  // Pasar los datos de Softr a tu aplicacin
  return <App softrData={softrData} />;
};

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <SoftrIntegration />
  </React.StrictMode>
);

// Enviar mensajes a Softr
function sendMessageToSoftr(data) {
  window.parent.postMessage({
    type: 'reactAppData',
    data
  }, 'https://www.tusitiosoftr.com');
} 