import React, { useEffect, useRef } from 'react';

const TwitterTimeline = ({ username, height = 600 }) => {
  const containerRef = useRef(null);
  const isMounted = useRef(true);
  const timelineAttempted = useRef(false);

  useEffect(() => {
    isMounted.current = true;
    
    return () => {
      isMounted.current = false;
    };
  }, []);

  useEffect(() => {
    // Si ya se intent cargar el timeline, no intentar de nuevo
    if (timelineAttempted.current) return;
    
    timelineAttempted.current = true;

    const createTimeline = () => {
      // Verificar que el componente sigue montado y el contenedor existe
      if (!isMounted.current || !containerRef.current) return;
      
      try {
        window.twttr.widgets.createTimeline(
          {
            sourceType: 'profile',
            screenName: username.replace('@', '')
          },
          containerRef.current,
          {
            height: height,
            chrome: 'noheader, nofooter',
            theme: 'light'
          }
        );
      } catch (error) {
        console.error('Error al intentar crear el timeline:', error);
      }
    };

    // Funcin para cargar el timeline cuando twttr est disponible
    const loadTimeline = () => {
      if (window.twttr && window.twttr.widgets) {
        createTimeline();
      } else {
        // Esperar a que el script de Twitter se cargue
        window.twttrCallbacks = window.twttrCallbacks || [];
        window.twttrCallbacks.push(createTimeline);
      }
    };

    // Intentar cargar el timeline
    loadTimeline();
  }, [username, height]);

  return (
    <div className="twitter-timeline-container">
      <div ref={containerRef} className="w-full h-full">
        <div className="flex items-center justify-center h-full">
          <p className="text-gray-500">Cargando timeline de Twitter...</p>
        </div>
      </div>
    </div>
  );
};

export default TwitterTimeline; 