import React, { useEffect, useRef } from 'react';

const EmergencyMap = ({ emergencies = [] }) => {
  const mapRef = useRef(null);
  const mapInstanceRef = useRef(null);

  // Coordenadas centrales de Chile (aproximadamente)
  const chileCenter = [-35.675147, -71.542969];
  
  useEffect(() => {
    // Asegurarse de que Leaflet est disponible
    const L = window.L;
    if (!L) {
      console.error('Leaflet no est cargado. Asegrate de incluir el script en index.html');
      return;
    }

    // Si ya existe una instancia del mapa, la limpiamos
    if (mapInstanceRef.current) {
      mapInstanceRef.current.remove();
    }

    // Crear el mapa si el contenedor existe
    if (mapRef.current && !mapInstanceRef.current) {
      // Inicializar el mapa centrado en Chile
      mapInstanceRef.current = L.map(mapRef.current).setView(chileCenter, 5);

      // Aadir la capa de OpenStreetMap
      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
      }).addTo(mapInstanceRef.current);

      // Aadir marcadores para las emergencias si existen
      if (emergencies.length > 0) {
        emergencies.forEach(emergency => {
          const emergencyIcon = L.divIcon({
            className: 'emergency-marker',
            html: `<div class="w-6 h-6 rounded-full bg-red-500 flex items-center justify-center text-white text-xs">
                    <i class="fas ${emergency.type === 'fire' ? 'fa-fire' : 'fa-car-crash'}"></i>
                  </div>`,
            iconSize: [24, 24],
            iconAnchor: [12, 12]
          });

          L.marker(emergency.coords, { icon: emergencyIcon })
            .addTo(mapInstanceRef.current)
            .bindPopup(`
              <b>${emergency.title}</b><br>
              ${emergency.description}<br>
              <small>${emergency.location}</small>
            `);
        });
      }
    }

    // Limpiar el mapa cuando el componente se desmonte
    return () => {
      if (mapInstanceRef.current) {
        mapInstanceRef.current.remove();
        mapInstanceRef.current = null;
      }
    };
  }, [emergencies, chileCenter]);

  return (
    <div ref={mapRef} className="h-full w-full rounded-lg overflow-hidden"></div>
  );
};

export default EmergencyMap; 